}

input {
  margin-right: 5px;
}
.App {
  text-align: center;
}

#root{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.main-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-bottom: 20px;
}

.gameboy{
  display: inline-block;
  padding: 45px;
  padding-top: 30px;
  padding-bottom: 20px;
  margin-top: 20px;
  background: rgb(255,190,0);
  border: 5px solid rgb(240,174,0);
  border-radius: 10px;
}

.reactris-container{
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 0;
}

.screen{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  position: relative;
  margin-bottom: 20px;
  padding: 5px;
  background: #9ead86;
  border: 5px solid;
  border-top-color: currentcolor;
  border-right-color: currentcolor;
  border-bottom-color: currentcolor;
  border-left-color: currentcolor;
  border-color: #987f0f rgb(255,210,0) rgb(255,205,0) #987f0f;
}
#scaler{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
  width: 0; /*calculated via js to keep board aspect ratio*/
  height: 100%;
  margin: 0 auto;
}
.table{
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0;
  border-bottom: 5px solid black;
}
.row{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0;
  margin: 0;
  height: 100%;
}
.col{
  width: 100%;
  position: relative;
  margin: 0;
  border: 2px solid #879372;
  padding: 0.8%; /*inside border*/
}
.col::after{
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: #8E9A79;
}
.filled{
  border-color: black;
}
.filled::after{
  background: black;
}
.empty{
  border-color: #879372;
}
.debug-container{
  position: absolute;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.debug-board{
  color: white;
  font-size: 12px;
  width: 100px !important; 
  height: 160px !important;
  padding-bottom: 20px;
}


@media(max-width: 768px){
  .main-container{
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
  }
  .gameboy{
    max-width: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0;
    border-radius: 0;
  }
  .debug-board{
    display: none;
  }
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.info{
    padding: 0;
    padding: 1px;
    padding-left: 5px;
    padding-right: 5px;
    margin: 4px -5px -5px -5px;
    background: #8e9d76;
    color: black;
